eza. También es un aro abierto de cualquier material que usan las mujeres como adorno, o para sujetarse el pelo hacia atrás, y se llama diadema asimismo al arco que llevan algunas coronas de un lado a otro por la parte superior. ")])]),_c('div',{staticClass:"col-lg-3 col-md-8"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema5/img-13.png")}})])])])])],1)]),_c('div',{staticClass:"row justify-content-center align-items-center mb-3 lnd-t",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-12 mb-4"},[_c('LineaTiempoD',{staticClass:"color-acento-botones"},[_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"numero":"13","titulo":"Esclava"}},[_c('div',{staticClass:"col-lg-9"},[_c('p',[_vm._v("Pulsera rígida más o menos ancha que debe su nombre a la costumbre romana de ponérsela los patrones a sus esclavas para reconocer a qué casa pertenecían. ")])]),_c('div',{staticClass:"col-lg-3 col-md-8"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema5/img-14.png")}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"numero":"14","titulo":"Gemelos"}},[_c('div',{staticClass:"col-lg-9"},[_c('p',[_vm._v("Juego de dos botones iguales, utilizados como cierre para puños de camisas y blusas, unidos por un corto vástago que pasa por los ojales. Los pendientes son adornos para el lóbulo, que pueden ser zarcillos (sin colgante) o arracadas (con colgante). Existen diversos cierres: aro, gancho, tornillo y presión. El casquillo es esencial para sujetar el colgante.")])]),_c('div',{staticClass:"col-lg-3 col-md-8"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema5/img-15.png")}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"numero":"15","titulo":"Pulsera"}},[_c('div',{staticClass:"col-lg-9"},[_c('p',[_vm._v("Es un cerco de metal u otra materia, con piedras preciosas o sin ellas, que rodea la muñeca o alguna parte del brazo y se usa como adorno. Como joya es una adaptación del brazalete, cuando se impuso la manga larga, que dejaba a éste fuera de lugar. ")])]),_c('div',{staticClass:"col-lg-3 col-md-8"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema5/img-16.png")}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"numero":"16","titulo":"Sortija"}},[_c('div',{staticClass:"col-lg-9"},[_c('p',[_vm._v("La palabra \"sortija\" proviene del latín \"sors-sortis\" y se refiere a un adorno para los dedos. La moda dicta llevar una o varias sortijas, generalmente en dedos descubiertos, a veces sobre guantes. Existen sortijas de sello, simbólicas como las de boda, y de insignia jerárquica, como los anillos episcopales, además de las puramente ornamentales.")])]),_c('div',{staticClass:"col-lg-3 col-md-8"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema5/img-17.png")}})])])])])],1)]),_c('p',{staticClass:"mb-4"},[_vm._v("Según su forma, uso o componentes, se adoptan diversos nombres para esta joya:")]),_vm._m(2)])],1)
